import Vue from "vue";
//import App from "./App.vue";
import store from "./store";
import router from "./router";

Vue.config.productionTip = false;
function loadScript(src) {
  return new Promise((resolv) => {
    var s = document.createElement("script");
    s.setAttribute("src", src);
    s.onload = function () {
      console.log(" Chargement du script ok : ", src);
      resolv(true);
    };
    document.head.appendChild(s);
  });
}

function loadstyle(src) {
  return new Promise((resolv) => {
    var s = document.createElement("link");
    s.setAttribute("rel", "stylesheet");
    s.setAttribute("href", src);
    s.onload = function () {
      console.log("Chargement du Style ok : ", src);
      resolv(true);
    };
    document.head.appendChild(s);
  });
}

const App = () =>
  import("./App.vue").then((component) => {
    return new Promise((resolv) => {
      const callback = () => {
        resolv(component);
      };
      // On verifie la presence de jQuery et de window.Papa;
      if (window.jQuery && window.Papa) {
        callback();
      } else {
        loadScript(
          "https://cdnjs.cloudflare.com/ajax/libs/jquery/3.6.0/jquery.min.js"
        ).then((status) => {
          if (status) {
            loadScript(
              "https://cdnjs.cloudflare.com/ajax/libs/PapaParse/5.3.1/papaparse.min.js"
            ).then((status2) => {
              if (status2)
                loadScript(
                  "https://cdnjs.cloudflare.com/ajax/libs/moment.js/2.29.1/moment-with-locales.min.js"
                ).then((status3) => {
                  if (status3)
                    loadScript(
                      "https://cdnjs.cloudflare.com/ajax/libs/jqueryui/1.12.1/jquery-ui.min.js"
                    ).then((status4) => {
                      if (status4)
                        loadstyle(
                          "https://cdnjs.cloudflare.com/ajax/libs/jqueryui/1.12.1/jquery-ui.min.css"
                        ).then((status4) => {
                          if (status4) {
                            callback();
                          }
                        });
                    });
                });
            });
          }
        });
      }
    });
  });
new Vue({
  store,
  router,
  render: (h) => h(App),
}).$mount("#app");
