import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    headerFile: [],
    rowsFile: [],
  },
  mutations: {
    SET_rowsFile(state, rows) {
      console.log("rows ", rows);
      state.rowsFile = rows;
    },
    SET_headerFile(state, fields) {
      state.headerFile = fields;
    },
  },
  actions: {
    setRowsFile({ commit }, rows) {
      commit("SET_rowsFile", rows);
    },
    setHeaderFile({ commit }, rows) {
      commit("SET_headerFile", rows);
    },
  },
  modules: {},
});
