import Vue from "vue";
import VueRouter from "vue-router";
//import importDataToDrupal from "../components/import-drupal/importDataToDrupal.vue";

Vue.use(VueRouter);
function loadScript(src) {
  return new Promise((resolv) => {
    var s = document.createElement("script");
    s.setAttribute("src", src);
    s.onload = function () {
      console.log(" Chargement du script ok : ", src);
      resolv(true);
    };
    document.head.appendChild(s);
  });
}
const routes = [
  {
    path: "/",
    name: "import data",
    component: () => {
      return new Promise((resolv) => {
        const callback = () => {
          resolv(import("../components/import-drupal/importDataToDrupal.vue"));
        };
        // On verifie la presence de jQuery et de window.Papa;
        if (window.Papa) {
          callback();
        } else {
          loadScript(
            "https://cdnjs.cloudflare.com/ajax/libs/PapaParse/5.3.1/papaparse.min.js"
          ).then((s) => {
            if (s) callback();
          });
        }
      });
    },
  },
  {
    path: "/delete-terms",
    name: "suppresion de terme",
    props: true,
    component: () => import("../components/DeleteTerms/DeleteTerms.vue"),
  },
  {
    path: "/*",
    redirect: "/",
  },
];

const router = new VueRouter({
  //mode: "history",
  //base: process.env.BASE_URL,
  mode: "hash",
  routes,
});

export default router;
